.loader {
  width: 48px;
  height: 48px;
  margin: auto;
  position: relative;
}

.loader:before {
  content: "";
  width: 48px;
  height: 5px;
  opacity: .25;
  background: #000;
  border-radius: 50%;
  animation: shadow .5s linear infinite;
  position: absolute;
  top: 60px;
  left: 0;
}

.loader:after {
  content: "";
  width: 100%;
  height: 100%;
  background: rgba(35, 26, 160, .706);
  border-radius: 4px;
  animation: bxSpin .5s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes bxSpin {
  17% {
    border-bottom-right-radius: 3px;
  }

  25% {
    transform: translateY(9px)rotate(22.5deg);
  }

  50% {
    border-bottom-right-radius: 40px;
    transform: translateY(18px)scale(1, .9)rotate(45deg);
  }

  75% {
    transform: translateY(9px)rotate(67.5deg);
  }

  100% {
    transform: translateY(0)rotate(90deg);
  }
}

@keyframes shadow {
  0%, 100% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.2, 1);
  }
}

